/*=====================
    Title Scss start
==========================*/
.marketing-title {
    margin-bottom: 50px;
    @media (max-width: 1400px) {
        margin-bottom: 20px;
    }
    h2 {
        width: 90%;
        text-transform: capitalize;
        @media (max-width: 1199px) {
            width: 100%;
        }
    }
    h5 {
        color: rgba(var(--theme-color3), 1);
        display: inline-block;
        position: relative;
        padding-left: 10px;
        &:after {
            content: "";
            height: 25px;
            left: 0;
            position: absolute;
            top: -3px;
            width: 44%;
            background-color: rgba(var(--theme-color3), 0.1);
            @media (max-width: 991px) {
                width: 62%;
            }
        }
    }
    p {
        font-size: calc(15px + (18 - 15) * ((100vw - 320px) / (1920 - 320)));
        font-weight: 500;
        margin-top: 12px;
        width: 88%;
        margin-bottom: 0;
        @media (max-width: 1199px) {
            width: 100%;
        }
    }
    &.text-center {
        h2 {
            width: 100%;
        }
        p {
            margin: 12px auto 0;
            width: 46%;
            @media (max-width: 1400px) {
                width: 56%;
            }
            @media (max-width: 1199px) {
                width: 66%;
            }
            @media (max-width: 1199px) {
                width: 100%;
            }
        }
    }
    &.text-light {
        h2 {
            color: rgba(var(--white), 1);
        }
        p {
            color: rgba(var(--white), 0.95);
        }
    }
    &.center-a-xl {
        @media (max-width: 1199px) {
            text-align: center;
            h2 {
                margin: 0 auto;
            }
            p {
                width: 70%;
                margin: 0 auto;
                @media (max-width: 991px) {
                    width: 100%;
                }
            }
        }
    }
}

// app showcase
.app-title {
    margin-bottom: calc(30px + (50 - 30) * ((100vw - 320px) / (1920 - 320)));
    &.text-center {
        h2 {
            &:after {
                transform: translateX(-50%);
                left: 50%;
            }
        }
    }
    .sub-title {
        color: rgba(var(--theme-color1), 1);
        position: relative;
        display: inline-block;
        gap: 10px;
        padding: 10px 14px;
        background-color: rgba(var(--theme-color1), 0.05);
        border-radius: 30px;
        font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
        font-weight: 500;
        line-height: 1.2;
        margin-bottom: 8px;
        > div {
            display: flex;
            align-content: center;
            gap: 10px;
        }
        .round {
            width: calc(14px + (20 - 14) * ((100vw - 320px) / (1920 - 320)));
            height: calc(14px + (20 - 14) * ((100vw - 320px) / (1920 - 320)));
            border: 2px solid rgba(var(--theme-color1), 1);
            border-radius: 100%;
            position: relative;
            display: inline-block;
            span {
                width: calc(5px + (7 - 5) * ((100vw - 320px) / (1920 - 320)));
                height: calc(5px + (7 - 5) * ((100vw - 320px) / (1920 - 320)));
                background-color: rgba(var(--theme-color1), 1);
                border-radius: 100%;
                display: block;
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
            }
        }
    }
    h2 {
        margin-bottom: 0;
        position: relative;
        font-family: $space-grotesk;
        text-transform: capitalize;
        font-weight: 700;
        line-height: 1.5;
        &:after {
            content: "";
            position: absolute;
            width: 65px;
            height: 6px;
            background-image: url("../svg/shapes/highlight-sm.svg");
            bottom: -5px;
            left: 0;
        }
    }
    p {
        margin-bottom: 0;
        margin-top: 20px;
        width: 50%;
        font-weight: 500;
        @media (max-width: 991px) {
            width: 100%;
        }
    }
    &.text-light {
        .sub-title {
            color: rgba(var(--white), 1);
            background-color: rgba(var(--white), 0.1);
            .round {
                border-color: rgba(var(--white), 1);
                span {
                    background-color: rgba(var(--white), 1);
                }
            }
        }
    }
}
