/*=====================
    switch Scss start
==========================*/

.switch-style-1 {
    input[type="checkbox"] {
        height: 0;
        width: 0;
        visibility: hidden;
    }

    label {
        cursor: pointer;
        text-indent: -9999px;
        width: 57px;
        height: 30px;
        background: url("../svg/shapes/toggle-bg.svg");
        background-size: cover;
        background-repeat: no-repeat;
        display: block;
        border-radius: 100px;
        position: relative;
        &:after {
            content: "";
            position: absolute;
            top: 3px;
            left: 3px;
            width: 24px;
            height: 24px;
            background: rgba(var(--theme-color3), 1);
            border-radius: 24px;
            transition: 0.3s;
        }
    }

    input:checked + label {
        background: url("../svg/shapes/toggle-bg.svg");
        background-size: cover;
        background-repeat: no-repeat;
    }

    input:checked + label:after {
        left: calc(100% - 5px);
        transform: translateX(-100%);
    }

    label:active:after {
        width: 45px;
    }
}
