/*=====================
    Back to top Scss start
==========================*/

.back-to-top {
    position: fixed;
    bottom: calc(10px + (20 - 10) * ((100vw - 320px) / (1920 - 320)));
    right: calc(20px + (30 - 20) * ((100vw - 320px) / (1920 - 320)));
    height: calc(40px + (50 - 40) * ((100vw - 320px) / (1920 - 320)));
    width: calc(40px + (50 - 40) * ((100vw - 320px) / (1920 - 320)));
    display: none;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    cursor: pointer;
    z-index: 1;
    transition: all 0.5s ease;
    span {
        height: calc(100% - 6px);
        width: calc(100% - 6px);
        background-color: rgba(var(--white), 1);
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 30px;
        color: rgba(var(--title-color), 1);
        box-shadow: 0px 5px 30px rgba(var(--theme-color2), 0.12);
        transition: all 0.5s ease;
    }
    &:hover {
        transition: all 0.5s ease;
        transform: translateY(-5px);
        span {
            box-shadow: 0px 7px 20px rgba(var(--theme-color2), 0.13);
            transition: all 0.5s ease;
        }
    }
}
